<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-form-item label="治安重点地区" prop="securityDistrict">
                        <el-input v-model="dialogForm.securityDistrict" :maxlength="25" placeholder="请填写治安重点地区"></el-input>
                    </el-form-item>
                    <el-form-item label="涉及区域范围" prop="invoiveDistrictScope">
                        <el-input v-model="dialogForm.invoiveDistrictScope" :maxlength="100" placeholder="请填写涉及区域范围"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="治安突出问题" prop="securityProblem">
                        <el-select placeholder="请选择治安突出问题" clearable v-model="dialogForm.securityProblem">
                            <el-option v-for="(item, index) in dic['治安突出问题']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="整治牵头单位" prop="renovateLeadDept">
                        <el-input v-model="dialogForm.renovateLeadDept" :maxlength="25" placeholder="请填写整治牵头单位"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="涉及区域类型" prop="invoiveDistrictType">
                        <el-select placeholder="请选择涉及区域类型" clearable v-model="dialogForm.invoiveDistrictType">
                            <el-option v-for="(item, index) in dic['涉及区域类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="整治参与单位" prop="renovateParticDept">
                        <el-input v-model="dialogForm.renovateParticDept" :maxlength="100" placeholder="请填写整治参与单位"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-form-item label="整治牵头单位负责人姓名" prop="initiatorName" label-width="180px">
                        <el-input v-model="dialogForm.initiatorName" :maxlength="12" placeholder="请填写整治牵头单位负责人姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="整治期间破获刑事案件数" prop="managerCrimeCount" label-width="180px">
                        <el-input v-model="dialogForm.managerCrimeCount" :maxlength="6" placeholder="请填写整治期间破获刑事案件数"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="整治牵头单位负责人电话" prop="initiatorContact" label-width="180px">
                        <el-input v-model="dialogForm.initiatorContact" :maxlength="12" placeholder="请填写整治整治牵头单位负责人电话"></el-input>
                    </el-form-item>
                    <el-form-item label="整治期间查处治安案件数" prop="managerPeaceCount" label-width="180px">
                        <el-input v-model="dialogForm.managerPeaceCount" :maxlength="6" placeholder="请填写整治期间查处治安案件数"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="整改时限" prop="rectifyTime" label-width="120px">
                        <el-date-picker v-model="dialogForm.rectifyTime" type="date" placeholder="请选择日期"></el-date-picker>
                    </el-form-item>  
                      <el-form-item label="效果评估" prop="effectAssess" label-width="120px">
                        <el-select placeholder="请选择效果评估" clearable v-model="dialogForm.effectAssess">
                            <el-option v-for="(item, index) in dic['效果评估']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                
                <el-col :span="12">
                    <el-form-item label="整治情况" prop="managerCaseInfo">
                        <el-input v-model="dialogForm.managerCaseInfo" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写整治情况"></el-input>
                    </el-form-item>                                      
                </el-col>
                <el-col :span="12">
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/district_check_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        治安突出问题: [],
        涉及区域类型: [],
        效果评估: []
      },
      disCodeOption: [],
      dialogForm: {
        initiatorName:"",
        initiatorContact:"",
        securityDistrict:"",
        securityProblem:"",
        invoiveDistrictType:"",
        invoiveDistrictScope:"",
        renovateLeadDept:"",
        renovateParticDept:"",
        rectifyTime:"",
        managerCrimeCount:"",
        managerPeaceCount:"",
        managerCaseInfov:"",
        effectAssess:"",
        districtCode:[]
      },
      teamRules: {
        securityDistrict: [
          { required: true, message: "请输入治安重点地区", trigger: "blur" }
        ],
        securityProblem: [
          { required: true, message: "请选择治安突出问题", trigger: "change" }
        ],
        invoiveDistrictType: [
          { required: true, message: "请选择涉及区域类型", trigger: "change" }
        ],
        invoiveDistrictScope: [
          { required: true, message: "请输入涉及区域范围", trigger: "blur" }
        ],
        initiatorName: [
          {
            required: true,
            message: "请输入整治牵头单位负责人姓名",
            trigger: "blur"
          }
        ],
        initiatorContact: [
          {
            required: true,
            message: "请输入整治牵头单位负责人电话",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        rectifyTime: [
          { required: true, message: "请选择整改时限", trigger: "change" }
        ],
        managerCrimeCount: [util.validate.valiInteger()],
        managerPeaceCount: [util.validate.valiInteger()],
        managerCaseInfo: [
          { required: true, message: "请输入整治情况", trigger: "blur" }
        ],
        effectAssess: [
          { required: true, message: "请选择效果评估", trigger: "change" }
        ],
        districtCode: [
          { required: true, message: "请选择区域编码", trigger: "change" }
        ]
      }
    };
  },
 watch: {
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  
  methods: {
 
    /** 保存 */
    submitForm() {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.rectifyTime = util.time.formatDate(param.rectifyTime,"YYYY-MM-DD");
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {
             
              // 添加 重点地区排查排查整治
              service.requestSaveDistrictCheck(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "重点地区排查排查整治添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else {
              service.requestUpdateDistrictCheck(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "重点地区排查排查整治修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
        
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {};
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>